<script>
    import { FormGroup, InputGroup, InputGroupText, Input } from 'sveltestrap';

    let x = 0;
    let chunkX = 0;
    let treasureX = 0;
    let z = 0;
    let chunkZ = 0;
    let treasureZ = 0;

    function calculateTreasureX(value)
    {
        chunkX = Math.floor(value / 16);
        treasureX = (chunkX + 0.5) * 16;
    }

    function calculateTreasureZ(value)
    {
        chunkZ = Math.floor(value / 16);
        treasureZ = (chunkZ + 0.5) * 16;
    }

    calculateTreasureX(x);
    calculateTreasureZ(z);
</script>

<h3>Treasure Location</h3>
<FormGroup>
    <InputGroup>
        <InputGroupText>X</InputGroupText>
        <Input bind:value={x} on:input="{e => calculateTreasureX(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
<FormGroup>
    <InputGroup>
        <InputGroupText>Z</InputGroupText>
        <Input bind:value={z} on:input="{e => calculateTreasureZ(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
Treasure buried at <code>{treasureX},{treasureZ}</code>