    import { Alert } from 'sveltestrap';
</script>

<h3>Custom Resource Packs on PS5</h3>

<Alert color="danger">
    <h4 class="alert-heading">Requirements</h4>
    <ul>
        <li>A PC with Minecraft Bedrock installed</li>
        <li>Internet connection on PC and PS5</li>
    </ul>
</Alert>

<h4>Steps</h4>
<ol>
    <li>
        On your PS5, download the Minecraft Classic Textures from the Minecraft Store. After it has downloaded and installed, delete the Classic Textures through the global resource packs screen.
    </li>
    <li>
        On your PC, install the Minecraft Classic Textures from the Minecraft Store. After it has downloaded and installed, delete the Classic Textures through the global resource packs screen.
    </li>
    <li>
        On your PC, import the resource pack you want to put on your PS4.
    </li>
    <li>
        On your PC, exit Minecraft and download the following <a href="build/assets/minecraft/manifest.json" target="_blank" download>manifest.json</a> file through your web browser.
    </li>
    <li>
        On your PC, copy the manifest.json file from your Downloads folder to your clipboard and locate the resource_packs folder. The resource_packs folder can be found at the following location<br />
        <code>%localappdata%\Packages\Microsoft.MinecraftUWP_8wekyb3d8bbwe\LocalState\games\com.mojang\resource_packs</code>
    </li>
    <li>
        On your PC, once you locate the resource_packs folder, locate the resource pack that you installed and paste the manifest.json file in that folder. Be sure to overwrite the existing manifest.json file.
    </li>
    <li>
        On your PC, launch Minecraft and apply the desired resource pack to any world. Start the world. Note that the resource pack name may say "pack.name" and the description may read "pack.description".
    </li>
    <li>
        On your PS5, join the world and make sure to download the texture pack when it prompts you. Once you join, you can leave the world.
    </li>
    <li>
        On your PS5, apply the Classic Textures to the world you want the custom resource pack applied on. It will ask you to re-download the Classic Textures. Click Download. When the world loads, you will see that the custom resource pack loads and not the Classic Textures.
    </li>
</ol>

<h4>Tips</h4>
<ul>
    <li>You can delete installed (cached) resource packs through the Storage tab in Settings in Minecraft.</li>
    <li>When updating a resource pack, delete the custom one in the Storage tab and the Classic Textures through the global resources screen. Follow this tutorial again from Step 1.</li>
    <li>You may only apply resource packs per-world using this method, not globally.</li>
</ul>

