<script>
    import { Form, FormGroup, InputGroup, InputGroupText, Input } from 'sveltestrap';

    let useShalkerBoxesChecked = false;
    let stackSize = 16;
    let shulkerBoxSize = 27;
    let totalItems = 0;
    let shulkerBoxes = 0;
    let stacks = 0;
    let items = 0;

    function calculateStacks(value)
    {
        if (value < 0)
        {
            value = 0;
        }

        let tmpStacks = value / stackSize;
        stacks = Math.floor(tmpStacks);
        items = Math.round((tmpStacks - stacks) * stackSize);

        if (useShalkerBoxesChecked)
        {
            let tmpShulkerBoxes = stacks / shulkerBoxSize;
            shulkerBoxes = Math.floor(tmpShulkerBoxes);
            stacks = Math.round((tmpShulkerBoxes - shulkerBoxes) * shulkerBoxSize);
        }
    }

    calculateStacks(totalItems);
</script>

<h3>Stack 16 Calculator</h3>
<FormGroup>
    <InputGroup>
        <InputGroupText>#</InputGroupText>
        <Input bind:value={totalItems} on:input="{e => calculateStacks(e.target.value)}" type=number min=0 />
    </InputGroup>
</FormGroup>
<div class="mb-3">
    <div class="form-check">
        <input class="form-check-input" type="checkbox" id="stack16shulkerBox" bind:checked={useShalkerBoxesChecked} on:change="{e => calculateStacks(totalItems) }" {useShalkerBoxesChecked}>
        <label class="form-check-label" for="stack16shulkerBox">in shulker boxes</label>
    </div>
</div>

Total:
<code>
    {#if useShalkerBoxesChecked > 0}
        {shulkerBoxes} {#if shulkerBoxes == 1}shulker box{:else}shulker boxes{/if},
    {/if}
    {stacks} {#if stacks == 1}stack{:else}stacks{/if},
    {items} {#if items == 1}item{:else}items{/if}
</code>