<script>
    import { Form, FormGroup, InputGroup, InputGroupText, Input, Table, Icon } from 'sveltestrap';

    let x = 0;
    let chunkX = 0;
    let minX = 0;
    let maxX = 15;
    let z = 0;
    let chunkZ = 0;
    let minZ = 0;
    let maxZ = 15;

    function calculateXRange(value)
    {
        chunkX = Math.floor(value / 16);
        minX = chunkX * 16;
        maxX = minX + 15;
    }

    function calculateZRange(value)
    {
        chunkZ = Math.floor(value / 16);
        minZ = chunkZ * 16;
        maxZ = minZ + 15;
    }

    calculateXRange(x);
    calculateZRange(z);
</script>

<h3>Borders</h3>
<FormGroup>
    <InputGroup>
        <InputGroupText>X</InputGroupText>
        <Input bind:value={x} on:input="{e => calculateXRange(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
<FormGroup>
    <InputGroup>
        <InputGroupText>Z</InputGroupText>
        <Input bind:value={z} on:input="{e => calculateZRange(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
This is chunk <code>{chunkX},{chunkZ}</code>
<Table bordered responsive>
    <caption>Chunk goes from <code>{minX},{minZ}</code> to <code>{maxX},{maxZ}</code></caption>
    <tbody>
        <tr>
            <td class="text-end"><code>{minX},{minZ}</code> <Icon name="arrow-up-left" /></td>
            <td><Icon name="arrow-up-right" /> <code>{maxX},{minZ}</code></td>
        </tr>
        <tr>
            <td class="text-end"><code>{minX},{maxZ}</code> <Icon name="arrow-down-left" /></td>
            <td><Icon name="arrow-down-right" /> <code>{maxX},{maxZ}</code></td>
        </tr>
    </tbody>
</Table>