<script>
    import { Form, FormGroup, InputGroup, InputGroupText, Input } from 'sveltestrap';

    let x = 0;
    let overWorldX = 0;
    let z = 0;
    let overWorldZ = 0;

    function calculateXLocation(value)
    {
        overWorldX = value * 8;
    }

    function calculateZLocation(value)
    {
        overWorldZ = value * 8;
    }

    calculateXLocation(x);
    calculateZLocation(z);
</script>

<h3>From Nether</h3>
<FormGroup>
    <InputGroup>
        <InputGroupText>X</InputGroupText>
        <Input bind:value={x} on:input="{e => calculateXLocation(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
<FormGroup>
    <InputGroup>
        <InputGroupText>Z</InputGroupText>
        <Input bind:value={z} on:input="{e => calculateZLocation(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
Over world coordinates: <code>{overWorldX},{overWorldZ}</code>