<script>
    import { Form, FormGroup, InputGroup, InputGroupText, Input } from 'sveltestrap';

    let x = 0;
    let idealX = 0;
    let netherX = 0;
    let z = 0;
    let idealZ = 0;
    let netherZ = 0;

    function calculateXCoordinates(value)
    {
        idealX = Math.floor(value / 8) * 8;
        netherX = idealX / 8;
    }

    function calculateZCoordinates(value)
    {
        idealZ = Math.floor(value / 8) * 8;
        netherZ = idealZ / 8;
    }

    calculateXCoordinates(x);
    calculateZCoordinates(z);
</script>

<h3>From Overworld</h3>
<FormGroup>
    <InputGroup>
        <InputGroupText>X</InputGroupText>
        <Input bind:value={x} on:input="{e => calculateXCoordinates(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
<FormGroup>
    <InputGroup>
        <InputGroupText>Z</InputGroupText>
        <Input bind:value={z} on:input="{e => calculateZCoordinates(e.target.value)}" type=number />
    </InputGroup>
</FormGroup>
Ideal Overworld Coordinates: <code>{idealX},{idealZ}</code><br />
Nether Coordinates: <code>{netherX},{netherZ}</code>