<script>
    import { Row, Col, Form, FormGroup, InputGroup, InputGroupText, Input } from 'sveltestrap';

    let pRedX = 314;
    let pRedZ = -400;
    let pRedThrowX = 331;
    let pRedThrowZ = -393;
    let pBlueX = 510;
    let pBlueZ = 327;
    let pBlueThrowX = 524;
    let pBlueThrowZ = 326;
    let strongholdX = 0;
    let strongholdZ = 0;

    calculateStronghold();

    function calculateStronghold()
    {
        let vRedX = 0;
        let vRedZ = 0;
        let vBlueX = 0;
        let vBlueZ = 0;
        let nBlue = 0;

        vRedX = pRedThrowX - pRedX;
        vRedZ = pRedThrowZ - pRedZ;
        console.log("vRedX: " + vRedX);
        console.log("vRedZ: " + vRedZ);
        
        vBlueX = pBlueThrowX - pBlueX;
        vBlueZ = pBlueThrowZ - pBlueZ;
        console.log("vBlueX: " + vBlueX);
        console.log("vBlueZ: " + vBlueZ);

        nBlue = ( (pBlueZ - pRedZ) * vRedX - (pBlueX - pRedX ) * vRedZ ) / ( vBlueX * vRedZ - vBlueZ * vRedX );
        console.log("nBlue: " + nBlue);

        strongholdX = Math.round(pBlueX + nBlue * vBlueX, 0);
        strongholdZ = Math.round(pBlueZ + nBlue * vBlueZ, 0);
    }
</script>

<h3>Stronghold location</h3>

<Row>
    <Col sm=6>
        <FormGroup>
            <InputGroup>
                <InputGroupText>X1</InputGroupText>
                <Input bind:value={pRedX} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
        <FormGroup>
            <InputGroup>
                <InputGroupText>Z1</InputGroupText>
                <Input bind:value={pRedZ} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
    </Col>
    <Col sm=6>
        <FormGroup>
            <InputGroup>
                <InputGroupText>X1 of enderpearl</InputGroupText>
                <Input bind:value={pRedThrowX} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
        <FormGroup>
            <InputGroup>
                <InputGroupText>Z1 of enderpearl</InputGroupText>
                <Input bind:value={pRedThrowZ} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
    </Col>
</Row>
<hr />

<Row>
    <Col sm=6>
        <FormGroup>
            <InputGroup>
                <InputGroupText>X2</InputGroupText>
                <Input bind:value={pBlueX} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
        <FormGroup>
            <InputGroup>
                <InputGroupText>Z2</InputGroupText>
                <Input bind:value={pBlueZ} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
    </Col>
    <Col sm=6>
        <FormGroup>
            <InputGroup>
                <InputGroupText>X2 of enderpearl</InputGroupText>
                <Input bind:value={pBlueThrowX} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
        <FormGroup>
            <InputGroup>
                <InputGroupText>Z2 of enderpearl</InputGroupText>
                <Input bind:value={pBlueThrowZ} on:input="{e => calculateStronghold()}" type=number />
            </InputGroup>
        </FormGroup>
    </Col>
</Row>
<hr />

Treasure stronghold is located at <code>{strongholdX},{strongholdZ}</code>

